@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,200');

$blue-light: #0081C2;
$dark-blue: rgb(21, 102, 151);
$facebook-blue: #3B5998;
$whatsapp-green: #25d366;

$light-gray: #E3E3E3;

$primary-color: $blue-light;
$primary-color--dark: $dark-blue;



.v-align--middle {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.section--light-gray {
    background-color: $light-gray !important;
}

// Tipography
.text-primary--dark {
    color: $primary-color--dark !important;
}

.text-color--facebook-blue {
    color: $facebook-blue;
}

.text-color--whatsapp-green {
    color: $whatsapp-green;
}